import React, { Component } from "react";
import PrimaryToggle from "./PrimaryToggle";
import { Col, Container, Row, Button } from "reactstrap";
import SecondaryToggle from "./SecondaryToggle";
import { corosivityDict, sortedEu, sortedAnsi } from "../Data/Data2.js"


import axios from "axios";
import FinalSelection from "./FinalSelection";

import { API_URL } from "../constants";
import ConfirmButton from "./ConfirmButton";
import ResetButton from "./ResetButton";
import ActuatorModal from "./ActuatorModal";
import AccessoriseToggle from "./AccessoriseToggle";

function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
}

class ValveSelection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            wasprimary: true,
            submitted: false,
            submitted_secondary: false,
            valves: [],
            valves_secondary: [],
            keys: {
                standard: [],
                type: [],
                temperature: [],
                size: [],
                corosivity: [],
                application: [],
                material: []
            },
            standard: "",
            type: "",
            temperature: "",
            size: "",
            corosivity: "",
            application: "",
            material: "",
            joining: "",
            accessorise: true,
            finished: false,
            selectedValve: null
        };
        this.setFinished = this.setFinished.bind(this)
        this.setSelectedValve = this.setSelectedValve.bind(this)
        this.submitted_handler = this.submitted_handler.bind(this)
        this.submitted_secondary_handler = this.submitted_secondary_handler.bind(this)
        this.standard_handler = this.standard_handler.bind(this)
        this.accessorise_handler = this.accessorise_handler.bind(this)
        this.temperature_handler = this.temperature_handler.bind(this)
        this.corosivity_handler = this.corosivity_handler.bind(this)
        this.size_handler = this.size_handler.bind(this)
        this.type_handler = this.type_handler.bind(this)
        this.joining_handler = this.joining_handler.bind(this)
        this.material_handler = this.material_handler.bind(this)
        this.application_handler = this.application_handler.bind(this)
        this.getValvesPrimary = this.getValvesPrimary.bind(this)
        this.getValvesSecondary = this.getValvesSecondary.bind(this)
        this.resetValves = this.resetValves.bind(this)
        this.resetPrimary = this.resetPrimary.bind(this)
        this.resetFinish = this.resetFinish.bind(this)
        this.resetSecondary = this.resetSecondary.bind(this)
    }

    valvesDidMount() {
        this.resetState();
    }

    getValvesPrimary() {

        //axios.get(API_URL).then(res=>(this.setState({ valves: res.data }, //console.log(res.data))))
        // axios.post(API_URL, {
        //     params: { application: this.state.application, standard: this.state.standard, temperature: this.state.temperature, corosivity: this.state.corosivity }
        // }).then(res => this.setState({ valves: res.data }))
        ////console.log(this.res.data)
        //console.log(this.props.myAllValves, this.state.application, this.state.standard, parseInt(this.state.temperature, 10), corosivityDict[this.state.corosivity], this.props.myAllValves.filter(valve => (valve.application == this.state.application &&
        //    valve.standard == this.state.standard &&
        //   parseInt(valve.temperature, 10) <= parseInt(this.state.temperature, 10)) &&
        //    corosivityDict[this.state.corosivity].includes(valve.corosivity)))
        //console.log(this.state.accessorise)

        if (!this.state.accessorise) {
            this.setState({
                valves: this.props.myAllValves.filter(valve => (valve.application == this.state.application &&
                    valve.standard == this.state.standard &&
                    parseInt(valve.temperature, 10) <= parseInt(this.state.temperature, 10)) &&
                    corosivityDict[this.state.corosivity].includes(valve.corosivity))
            })
        }
        else {
            this.setState({
                valves: this.props.myAllValves.filter(valve => (valve.application == this.state.application &&
                    valve.standard == this.state.standard &&
                    parseInt(valve.temperature, 10) <= parseInt(this.state.temperature, 10)) &&
                    corosivityDict[this.state.corosivity].includes(valve.corosivity) &&
                    valve.type != "CHECK")
            })
        }
        this.setState({ wasprimary: true })
    };

    getValvesSecondary() {
        this.setState({ valves: this.state.valves.filter(valve => valve.size == this.state.size && valve.type == this.state.type) })
        this.setState({ wasprimary: false })
    };

    resetState = () => {
        this.getValves();
    };

    setSelectedValve(e) {
        this.setState({ selectedValve: e })
    }

    setFinished(e) {
        this.setState({ finished: true })
    }

    resetValves(e) {
        this.setState({ submitted: false })
        this.setState({ submitted_secondary: false })
    }

    resetPrimary(e) {
        this.setState({ type: "" })
        this.setState({ size: "" })
        this.setState({ valves: this.props.myAllValves })
        this.setState({ submitted: false })
        this.setState({ submitted_secondary: false })
    }

    resetFinish() {
        this.setState({ type: "" })
        this.setState({ size: "" })
        this.setState({ valves: this.props.myAllValves })
        this.setState({ finished: false })
        this.setState({ submitted: false })
        this.setState({ submitted_secondary: false })
    }
    resetSecondary(e) {
        this.setState({ submitted: true })
        this.setState({ submitted_secondary: false })
        this.getValvesPrimary()
    }

    standard_handler(e) {
        this.setState({ standard: e })
    }


    accessorise_handler(e) {
        this.setState({ accessorise: !e })

    }

    application_handler(e) {
        this.setState({ application: e })
    }

    temperature_handler(e) {
        this.setState({ temperature: e })
    }

    corosivity_handler(e) {
        this.setState({ corosivity: e })
    }

    submitted_handler(e) {
        this.setState({ submitted: e })
    }

    submitted_secondary_handler(e) {
        this.setState({ submitted_secondary: e })
    }

    size_handler(e) {
        this.setState({ size: e })
    }

    joining_handler(e) {
        this.setState({ joining: e })
    }

    type_handler(e) {
        this.setState({ type: e })
    }

    material_handler(e) {
        this.setState({ material: e })
    }

    temperatureMenu() {
        if (this.state.submitted === true) {
            return [this.state.temperature];
        }
        return ["-30", "-40", "-60"];
    }
    applicationMenu() {
        if (this.state.submitted === true) {
            return [this.state.application];
        }
        return ['Oil Sampling valve',
            'Valve for draining or/and filling',
            'Valve for Air Drying Unit',
            'Valve for Piping',
            'Valve for vacuuming',
            'Monitoring valves',
            'Special valves and accessories']

    }

    corosivityMenu(arg) {
        if (this.state.submitted === true) {
            return [this.state.corosivity];
        }
        return arg
    }

    standardMenu(arg) {
        if (this.state.submitted === true) {
            return [this.state.standard];
        }
        return ["EN", "ANSI"]
    }

    render() {
        var sizes = []
        var materials = []
        var types = []
        var joinings = []

        for (var step = 0; step < this.state.valves.length; step++) {
            sizes.push(this.state.valves[step].size)
            materials.push(this.state.valves[step].material)
            joinings.push(this.state.valves[step].joining)
            types.push(this.state.valves[step].type)
        }

        var uniquesizes = []

        if (this.state.standard === "EN") {

            for (var step = 0; step < sortedEu.length; step++) {
                if (sizes.includes(sortedEu[step]) && !uniquesizes.includes(sortedEu[step])) {
                    uniquesizes.push(sortedEu[step])
                }
            }

        }

        if (this.state.standard === "ANSI") {
            for (var step = 0; step < sortedAnsi.length; step++) {
                if (sizes.includes(sortedAnsi[step]) && !uniquesizes.includes(sortedAnsi[step])) {
                    uniquesizes.push(sortedAnsi[step])
                }
            }

        }


        var uniquematerials = [materials.filter(onlyUnique)]
        var uniquetypes = [types.filter(onlyUnique)][0]
        var uniquejoinings = [joinings.filter(onlyUnique)][0]




        return (

            <Container fluid align="center" style={{ marginTop: "30px" }}>


                <PrimaryToggle
                    disabled={this.state.submitted}
                    doshow={true}
                    handler={this.application_handler}
                    resetValves={this.resetValves}
                    name="Application"
                    resetAccessories = {this.accessorise_handler}
                    options={this.applicationMenu()} />
                <AccessoriseToggle
                    disabled={this.state.submitted}

                    handler={this.accessorise_handler}
                    doshow={this.state.application == "Valve for Piping"}
                ></AccessoriseToggle>
                <PrimaryToggle
                    disabled={this.state.submitted}
                    doshow={true}
                    handler={this.corosivity_handler}
                    resetValves={this.resetValves}
                    name="Corosivity"
                    options={this.corosivityMenu(corosivityDict["C3"].slice().reverse())} />
                <h5></h5>
                <PrimaryToggle
                    disabled={this.state.submitted}
                    doshow={true}
                    handler={this.temperature_handler}
                    resetValves={this.resetValves}
                    name="Temperature"
                    options={this.temperatureMenu()} />
                <PrimaryToggle
                    disabled={this.state.submitted}
                    doshow={true}
                    handler={this.standard_handler}
                    name="Standard"
                    resetValves={this.resetValves}
                    options={this.standardMenu()} />
                <Row>
                    <Col>
                        <ConfirmButton
                            doshow={true}

                            disabled={this.state.submitted}
                            handler={this.getValvesPrimary}
                            subreset={this.submitted_handler}
                            visible={this.state.standard == "" || this.state.application == "" || this.state.temperature == "" || this.state.corosivity == ""} />
                    </Col>
                    <Col>
                        <ResetButton
                            doshow={true}
                            handler={this.resetPrimary}
                            visible={this.state.submitted} />
                    </Col>
                </Row>

                <SecondaryToggle
                    disabled={this.state.submitted_secondary}

                    doshow={this.state.submitted}
                    handler={this.size_handler}
                    name="Size"
                    options={uniquesizes} />

                <SecondaryToggle
                    disabled={this.state.submitted_secondary}
                    doshow={this.state.submitted}
                    handler={this.type_handler}
                    name="Type"
                    options={uniquetypes} />
                {/*<SecondaryToggle
                    disabled={this.state.submitted_secondary}

                    doshow={this.state.valves.length}
                    handler={this.joining_handler}
                    name="Joining"
                    options={uniquejoinings} /> */}

                <Row>
                    <Col>
                        <ConfirmButton
                            doshow={this.state.submitted}
                            disabled={this.state.submitted_secondary}
                            handler={this.getValvesSecondary}
                            subreset={this.submitted_secondary_handler}
                            visible={this.state.size == "" || this.state.type == ""} />
                    </Col>
                    <Col>
                        <ResetButton
                            doshow={this.state.submitted}
                            handler={this.resetSecondary}
                            visible={this.state.submitted_secondary} />
                    </Col>
                </Row>
                <hr></hr>
                <Row>
                    <FinalSelection
                        valves={this.state.valves}
                        finisheHandler={this.setFinished}
                        visible={this.state.submitted_secondary}
                        setSelectedValve={this.setSelectedValve}
                    ></FinalSelection>
                </Row>
                {/* <Row>
                    <Col>
                        <ComponentList
                            //standard = {}
                            sizes={sizes}
                            components={this.state.valves}
                        //resetState={this.resetState}
                        />
                    </Col>
                </Row> */}
                <hr></hr>
                <ActuatorModal
                    is_disabled={!this.state.finished || !this.state.submitted_secondary || !this.state.submitted}
                    clickFirst={this.props.addHandler}
                    clickSecond={this.resetFinish}
                    valveInfo={[this.state.selectedValve, this.state.temperature, this.state.corosivity]}
                ></ActuatorModal>
                {/* <Button  onClick={() => {
                    this.props.addHandler(this.state.selectedValve, this.state.temperature, this.state.corosivity); this.resetFinish()
                }}>Add to Project</Button> */}
            </Container>
        );
    }

}


export default ValveSelection;


