import React, { Component } from "react";
import { Button, Input, ButtonGroup } from 'reactstrap';
import "./ProjectMenu.css";
import { Document, Page } from 'react-pdf'
import ProjectName from "./ProjectName.js";

//import zip from "../zip.js/WebContent/zip.js";
import MyApp from "./MyApp";
import Catalogue from "./Catalogue";

import jsPDF from 'jspdf';
import 'jspdf-autotable'

// import JSZip from 'jszip';
// import JSZipUtils from 'jszip'
class ProjectMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      projectName: "",
      manufacturer: "",
      customer: "",
      RAL: "",
      value: 1
    }
    this.setProjectName = this.setProjectName.bind(this)
    this.setManufacturer = this.setManufacturer.bind(this)

    this.setCustomer = this.setCustomer.bind(this)

    this.handleChange = this.handleChange.bind(this);
  }




  handleChange(event, valve) {
    this.props.editValves(event.target.value, valve);
  }
  ralChange(event, valve) {
    this.props.editValveRAL(event.target.value, valve);
  }
  setProjectName(value) {
    this.setState({ projectName: value })
  }
  setCustomer(value) {
    this.setState({ customer: value })
  }
  setManufacturer(value) {
    this.setState({ manufacturer: value })
  }

  returnTooltip(valve) {
    return "Material: " + valve.material + "\n" +
      "Corrosivity: " + valve.corosivity + "\n" +
      "Size: " + valve.size + "\n" +
      "Temperature: " + valve.temperature + "\n" +
      "Operation: " + valve.actuator

  }
  dummy(value) {
    console.log(value)
  }

  ismaterial(value) {
    return (value.material == "WCB" ||
      value.material == "GGG40" ||
      value.material == "GGG40.3" ||
      value.material == "GSC-25")
  }
  showTitle() {
    return (
      <div className="svi">
        <div className="brojachvalve">
          Valve
            </div>
        <div className="brojacbroj">
          #

            </div>
        <div className="brojachoutside">

          Outside Coating

        </div>
        <div className="brojachinside">

          Inside Coating

</div>
      </div>
    )
  }
  showManually() {
    if (this.props.manualValves.length > 0) {
      return (
        this.props.manualValves.map((valve, idx) => (
          <div className="manualni">

            <div className="manualnibotun">
              <span title={this.returnTooltip(valve)}>
                {valve.code}
              </span>
            </div>

            <div className="manualnibrojacha">
              {valve.counter}
            </div>
            <div className="manualnibrojachb">
              <Button color="danger" onClick={this.props.removeManually.bind(this, idx)}>Remove</Button>
            </div>
          </div>


        ))

      )
    }


  }
  showValves() {
    if (this.props.valves.length > 0) {
      return (
        this.props.valves.map(valve => (
          <div className="svi">
            <div className="botun">
              <span title={this.returnTooltip(valve)}>
                <MyApp
                  buttontext={valve.code}
                  title={valve.code}
                  pdf={valve.pdf}></MyApp>
              </span>
            </div>

            <div className="brojach">
              <input className="valve-input" id={"counter" + valve.id.toString()} type="number" min="1" step="1" value={valve.counter} onChange={(e) => this.handleChange(e, valve)}
              ></input>
            </div>
            <div className="brojach">
              <input className="ralich-input" style={{ border: valve.border }}
                id={"ral" + valve.id.toString()} type="text" maxlength="5"
                value={valve.ral} onChange={(e) => this.ralChange(e, valve)}
              ></input>


            </div>
            <div className="brojachinput">
              <input type="checkbox" checked={this.ismaterial(valve)} onChange={(e) => { this.props.editCoating(e.target.checked, valve) }}>

              </input>
            </div>
            <div className="brojach">
              <Button color="danger" onClick={this.props.editValves.bind(this, "0", valve)}>Remove</Button>
            </div>
          </div>
        ))
      )
    }

    if (this.props.manualValves.length > 0) {
      return (<div></div>)
    }
    return (


      <h4>
        Please add valves to the project
      </h4>


    )
  }
  createPDF() {

    var doc = new jsPDF('l')
    // It can parse html:
    //doc.setFontSize(18)
    doc.setFontSize(11)
    doc.setTextColor(100)
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();

    today = dd + '/' + mm + '/' + yyyy;
    // jsPDF 1.4+ uses getWidth, <1.4 uses .width
    doc.text(today, doc.internal.pageSize.width - 30, 10)
    doc.text("Processed by: " + this.props.username, 5, 10)
    doc.text("Mail: hidden", 5, 15)

    doc.setFontSize(14)
    doc.setTextColor(0)

    doc.text("Manufacturer: " + this.state.manufacturer, 5, 50)
    doc.text("End Customer / Country of Installation: " + this.state.customer, 5, 57.5)
    doc.setFontSize(22)
    doc.setTextColor(0)

    var text = "Valve order for " + this.state.projectName + " project"

    let xOffset = (doc.internal.pageSize.width / 2);
    doc.text(text, xOffset, 30, 'center')

    // doc.setFontSize(12)
    // let textWidth = doc.getTextWidth("Project info:");
    // doc.line(5, 45, 5 + textWidth, 45)
    // doc.text("Project info:", 5, 45)

    // doc.setFontSize(16)
    // xOffset = (doc.internal.pageSize.width / 14);
    // text = "TEMPERATURE"
    // doc.text(text, xOffset * 3, 55, 'center')
    // text = "CORROSION CLASS"
    // doc.text(text, xOffset * 7, 55, 'center')
    // text = "RAL"
    // doc.text(text, xOffset * 11, 55, 'center')
    // text = this.props.valves[0].temperature
    // doc.text(text, xOffset * 3, 65, 'center')
    // text = this.props.valves[0].corosivity
    // doc.text(text, xOffset * 7, 65, 'center')
    // text = "1101"
    // doc.text(text, xOffset * 11, 65, 'center')
    doc.setFontSize(14)
    doc.setTextColor(0)

    doc.text("Table generated with the app: ", 5, 72.5)
    var mybody = []
    this.props.valves.map((valve, index) => (
      mybody.push([index + 1, valve.code, valve.counter, valve.application, valve.corosivity, valve.temperature,
      valve.material, valve.size, valve.pressure, valve.joining, valve.type, valve.actuator, valve.ral, valve.coating, valve.locking])
    ))
    // Or use javascript directly:
    doc.setFontSize(8)
    doc.autoTable({
      head: [["#", 'Code', 'Amount', "Application", 'C. C', 'Temp.', 'Material', 'Size', 'Pressure', "Connection", "Type", "Operation",
        "Outside Coating", "Inside Coating", "Locking & O/C"]],
      body: mybody,
      startY: 75
    })





    doc.setFontSize(14)
    doc.setTextColor(0)

    doc.text("Table generated manually by the user: ", 5, doc.autoTable.previous.finalY + 7.5)
    doc.setFontSize(8)

    var manualbody = []
    this.props.manualValves.map((valve, index) => (
      manualbody.push([index + 1, valve.code, valve.counter, valve.corosivity, valve.temperature,
      valve.material, valve.size,  valve.actuator, valve.ral, valve.coating])
    ))
    doc.autoTable({
      head: [["#", 'Code', 'Amount', 'C. C', 'Temp.', 'Material', 'Size', "Operation",
        "Outside Coating", "Inside Coating"]],
      body: manualbody,
      startY:  doc.autoTable.previous.finalY + 10


    })
    doc.setFontSize(10)
    doc.setTextColor(10)

    doc.text("This order has been generated using OMV Indoil Valve selection app and must be confirmed by the customer.", 14, doc.autoTable.previous.finalY + 10)
    doc.save(this.state.projectName + ".pdf")

    // JSZipUtils.getBinaryContent("pdfs/1.pdf", function (err, data) {
    //   if (err) {
    //     throw err; // or handle the error
    //   }
    // })
    // var zip = new JSZip();
    // zip.file("Some.pdf", data, { binary: true });
    // content = zip.generate();


  }
  sendMail() {
    var customer, body, quantities, product_codes;
    body = "Dear OMV-INDOIL,\nI would like to order:\n";
    this.props.valves.map(valve => (
      body += valve.counter + " of: " + valve.code + " " + "" + valve.corosivity + " " + valve.temperature + " " +
      valve.material + " " + valve.size + " " + valve.pressure + " " + valve.joining +
      " " + valve.type + " " + valve.actuator + " " + valve.ral + "\n"
    ))


    body += "\nYours truly,\nhackerman"
    body += '\n\n\nThis message is generated automatically using OMV Valve Selection App'

    var subject = this.state.projectName + " Order";

    window.open('mailto:dummy@dummy.com?body=' + encodeURIComponent(body) + '&subject=' + encodeURIComponent(subject));

    //zip.workerScriptsPath = '../zip.js/WebContent/';

  }



  render() {
    return (
      <div className="full">
        <h6></h6>
        <ProjectName
          placeholder={"Type in project number and name"}
          nameHandler={this.setProjectName} />
        <ProjectName
          placeholder={"Type in Transformer manufacturer"}
          nameHandler={this.setManufacturer} />
        <ProjectName
          placeholder={"Type in End customer / Country of installation"}
          nameHandler={this.setCustomer} />
        <div>

          <span>{this.showTitle()}</span>
        </div>
        <div className="scrollich">
          <span>{this.showValves()}</span>
          <span>{this.showManually()}</span>

        </div>
        <h3></h3>
        <div className="custombottom btn-group btn-group-justified">
          <div className="custombottom-center">
            <Button disabled={!(this.state.projectName != "") || (this.props.valves.length <= 0 && this.props.manualValves.length<=0)} color="primary" onClick={this.createPDF.bind(this)}>Generate PDF</Button>
          </div>
          <div className="custombottom-center">
            <Button disabled={true} color="primary" onClick={this.sendMail.bind(this)}>Send Mail</Button>
          </div>
          <div className="custombottom-center">
            <Catalogue
          addValve ={this.props.addManually}
          customthis = {this}
            ></Catalogue>
          </div>
          <div className="custombottom-center">
            <Button disabled={this.props.valves.length <= 0 && this.props.manualValves.length <= 0 } color="danger" onClick={this.props.removeAll.bind(this)}>Remove All</Button>
          </div>
        </div>
      </div>
    );
  }
}

export default ProjectMenu;