
import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Label, Input } from 'reactstrap';
import "./Catalogue.css";
import ManualValves from "./ManualValves.js";
import { FormGroup } from '@material-ui/core';
const dummy = (dummd) => {
  return 0;
}
const myvalve = {
  code: "",
  material: "",
  temperature: "",
  corosivity: "",
  size: "",
  ral: "",
  coating: "No",
  actuator: "",
  counter: ""
};

const setValveCode = (value) => {
  myvalve.code = value
}
const setValveMaterial = (value) => {
  myvalve.material = value
}
const setValveTemperature = (value) => {
  myvalve.temperature = value
}
const setValveCorosivity = (value) => {
  myvalve.corosivity = value
}
const setValveSize = (value) => {
  myvalve.size = value
}
const setValveRal = (value) => {
  myvalve.ral = value
}
const setValveCoating = (value) => {
  if (value && value!="No") {
    myvalve.coating = "Yes"

  }
  else {
    myvalve.coating = "No"

  }
}
const setValveCounter = (value) => {
  myvalve.counter = value
}
const setValveActuator = (value) => {
  myvalve.actuator = value
}
const Catalogue = (props) => {
  const {
    buttonLabel,
    className
  } = props;
  const [modal, setModal] = useState(false);
  const isempty = ()=>{
    console.log(myvalve)
  
    return (myvalve.code=="" || myvalve.counter== "")
  }
  const toggle = () => {
    setModal(!modal)
    setValveCode("")
    setValveSize("")
    setValveMaterial("")
    setValveTemperature("")
    setValveCorosivity("")
    setValveCoating("No")
    setValveRal("")
    setValveActuator("")
    setValveCounter("")
  }
  return (
    <div>
      <Button color="info" onClick={toggle}>Catalogue</Button>
      <Modal isOpen={modal} toggle={toggle} className={className} contentClassName="custom-catalogue-style">
        <ModalHeader toggle={toggle}>View Catalogue and Add Manually</ModalHeader>
        <ModalBody >
          <object data="pdfs/catalogue.pdf" width="100%" height="100%"></object>

        </ModalBody>
        <ModalFooter>
          {/* <Button color="primary" onClick={toggle}>Do Something</Button>{' '} */}
          <FormGroup row={true}>
            <ManualValves
              placeholder={"Valve Code"}
              valve={myvalve}
              nameHandler={setValveCode} />
            <ManualValves
              placeholder={"Valve Material"}
              nameHandler={setValveMaterial} />
            <ManualValves
              placeholder={"Temperature"}
              nameHandler={setValveTemperature} />
            <ManualValves
              placeholder={"Size"}
              nameHandler={setValveSize} />
            <ManualValves
              placeholder={"Corosion Class"}
              nameHandler={setValveCorosivity} />
            <ManualValves
              placeholder={"Outside Coating"}
              nameHandler={setValveRal} />
            <Label size="lg" check>
              <div className="outsidecoating">
                Inside Coating
              </div>
              <div className="coatoutside">
                <Input size="lg" type="checkbox" disabled={props.disabled} onChange={(e) => {setValveCoating(e.target.checked)}} />
              </div>
            </Label>
            <ManualValves
              placeholder={"Operation Option"}
              nameHandler={setValveActuator} />
            <ManualValves
              placeholder={"Amount"}
              nameHandler={setValveCounter} />
          </FormGroup>
          <Button disabled = {false} onClick={props.addValve.bind(props.customthis, myvalve)} color="primary">Add Valve</Button>

          <Button color="secondary" onClick={toggle}>Cancel</Button>
        </ModalFooter>
      </Modal>
    </div>
  );
}

export default Catalogue;