  setLoggedInText(value) {
    this.setState({ loggedInText: value })
  }
  editValveCoating(value, myvalve) {
    let valvez = this.jsonCopy(myvalve)

    let copyvalves = this.state.addedValves;

    let check = copyvalves.findIndex(valve => (valve.id === valvez.id
      && valve.corosivity === valvez.corosivity &&
      valve.temperature === valvez.temperature &&
      valve.actuator === valvez.actuator))
    let myral = "RAL" + value
    if (value) {
      copyvalves[check].coating = "Yes"

    }
    else {
      copyvalves[check].coating = "No"

    }
    this.setState({
      addedValves: copyvalves
    })


  }

  removeManually(idx) {
    console.log(idx)
    let copyvalves = this.state.addedManually;
    copyvalves.splice(idx, 1)
    this.setState({ addedManually: copyvalves })

  }

  removeValves() {
    if (window.confirm("You are about to remove ALL valves from the project. Are you sure?")) {
      this.setState({ addedValves: [] })
      this.setState({ addedManually: [] })

    }
  }

  editValves(myvalue, myvalve) {
    let valvez = this.jsonCopy(myvalve)

    let copyvalves = this.state.addedValves;
    let value = parseInt(myvalue)

    if (copyvalves.length > 0) {
      let check = copyvalves.findIndex(valve => (valve.id === valvez.id
        && valve.corosivity === valvez.corosivity &&
        valve.temperature === valvez.temperature &&
        valve.actuator === valvez.actuator))

      if (value <= 0 || value == NaN) {
        if (window.confirm("You are about to remove " + copyvalves[check].code + ". Are you sure?")) {
          copyvalves.splice(check, 1)
        }
      }
      else {
        copyvalves[check].counter = value
      }

      this.setState({
        addedValves: copyvalves
      })
    }
  }

  editValveRAL(value, myvalve) {
    let valvez = this.jsonCopy(myvalve)

    let checkvalue = parseInt(value)
    let copyvalves = this.state.addedValves;

    let check = copyvalves.findIndex(valve => (valve.id === valvez.id
      && valve.corosivity === valvez.corosivity &&
      valve.temperature === valvez.temperature &&
      valve.actuator === valvez.actuator))
    let myral = "RAL" + value
    if (myral in rals) {
      copyvalves[check].border = "3px solid " + rals[myral]
    }
    else {

      copyvalves[check].border = "1px #000000 dotted"

    }
    copyvalves[check].ral = value
    this.setState({
      addedValves: copyvalves
    })


  }

  jsonCopy(src) {
    return JSON.parse(JSON.stringify(src));
  }

  addManually(myvalue) {
    let value = this.jsonCopy(myvalue)

    if (value.code != "" && value.counter != "") {
      this.setState({
        addedManually: this.state.addedManually.concat([value])
      })
    }
  }







  addValve(myvalue, temperature, corosivity, actuator) {
    let value = this.jsonCopy(myvalue)
    ////console.log(value)
    if (value.material == "WCB" ||
      value.material == "GGG40" ||
      value.material == "GGG40.3" ||
      value.material == "GSC-25") {
      value.coating = "Yes"
    }
    else {
      value.coating = "No"
    }
    value.temperature = temperature
    value.corosivity = corosivity
    value.actuator = actuator
    ////console.log(value)

    let copyvalves = this.state.addedValves.slice();
    //////console.log(copyvalves, this.state.addedValves)
    let check = copyvalves.findIndex(valve => (valve.id === value.id
      && valve.corosivity === value.corosivity &&
      valve.temperature === value.temperature &&
      valve.actuator === value.actuator))
    //////console.log(copyvalves, this.stateaddedValves)

    //console.log(check, value, copyvalves[check])
    if (check >= 0) {
      // //console.log(copyvalves[check].id === value.id
      //   , copyvalves[check].corosivity === value.corosivity,
      //   copyvalves[check].temperature === value.temperature,
      //   copyvalves[check].actuator === value.actuator)
    }
    if (check >= 0) {
      //let idx = copyvalves.findIndex(valve => valve.id === value.id)
      alert("You have already added this valve. We have increased its counter by one.")
      copyvalves[check].counter = parseInt(copyvalves[check].counter) + 1

      this.setState({
        addedValves: copyvalves
      })
    }
    else {
      value.counter = 1;
      value.ral = "";
      value.border = "1px #000000 dotted"

      this.setState({
        addedValves: this.state.addedValves.concat([value])
      })
    }

  }

  passcheck(myUsername, myPass) {
    //console.log(myUsername, myPass)
    myUsername = myUsername.trim()
    if (myPass.length > 6) {
      if (myPass[0] === "f" && myPass[4] === "9" && this.state.usernames.includes(myUsername)) {
        this.setState({
          loggedIn: true,
          username: myUsername
        })
        return true
      }
    }
    return false
  }
