
import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, ButtonGroup } from 'reactstrap';
import "./ActuatorModal.css";

const ActuatorModal = (props) => {
  const {
    buttonLabel,
    className
  } = props;
  const [rSelected, setRSelected] = useState(null);
  const mylist = ["No Operation", "Bare Shaft", "Hand Lever", "Hand Wheel", "Gearbox", "Actuator"]
  const imgarray = ["placeholder", "1", "2", "3", "4", "5"]

  const [modal, setModal] = useState(false);
  var selected = null
  const toggle = () => setModal(!modal);
  if (!modal && rSelected != null) {
    setRSelected(null)
  }
  const myClick = (actuator) => (

    props.clickFirst(props.valveInfo[0], props.valveInfo[1], props.valveInfo[2], actuator),
    props.clickSecond(),
    toggle()
    //console.log(rSelected, x, rSelected===1)
  )
  const views = []
  if (props.valveInfo[0]) {

    for (var step = 0; step < props.valveInfo[0].operation.length; step++) {
      let y = step;
      if (mylist[props.valveInfo[0].operation[y]] != "No Operation") {
        views.push(
          <div className="tooltipz-act">
            <Button color="primary" onClick={() => setRSelected(y)} active={rSelected == y}>{mylist[props.valveInfo[0].operation[y]]}</Button>
            <span>

              <img className="callout-act" src={"images/pogoni/" + imgarray[props.valveInfo[0].operation[y]] + "_hd.png"} alt="" />
               </span>
          </div>)
      }
      else {
        views.push(
          <div className="tooltipz-act">
            <Button color="primary" onClick={() => setRSelected(y)} active={rSelected == y}>{mylist[props.valveInfo[0].operation[y]]}</Button>
          </div>
        )
      }

      

    }
  }
{/* <Button  color="primary" onClick={() =>   setRSelected(0)} active={rSelected == 0}>Bare shaft</Button>
<Button  color="primary" onClick={() =>  setRSelected(1)} active={rSelected == 1}>Gearbox</Button>
<Button  color="primary" onClick={() =>   setRSelected(2)} active={rSelected == 2}>Handle</Button>
<Button  color="primary" onClick={() =>   setRSelected(3)} active={rSelected == 3}>Electric actuator</Button> */}
return (
  <div>
    <Button color="info" disabled={props.is_disabled} onClick={toggle}>Add to project</Button>
    <Modal isOpen={modal} toggle={toggle} className={className} contentClassName="actuator-style">
      <ModalHeader toggle={toggle}>Choose operation option</ModalHeader>
      <ModalBody >
        <ButtonGroup>
        {views}
        </ButtonGroup>

      </ModalBody>
      <ModalFooter>
        {/* <Button color="primary" onClick={toggle}>Do Something</Button>{' '} */}
        <Button color="secondary" disabled={rSelected === null} onClick={() => myClick(mylist[props.valveInfo[0].operation[rSelected]])}>Submit</Button>

        <Button color="secondary" onClick={toggle}>Cancel</Button>
      </ModalFooter>
    </Modal>
  </div>
);
}
// is_disabled={!this.state.finished || !this.state.submitted_secondary || !this.state.submitted}
// clickFirst={this.props.addHandler}
// clickSecond={this.resetFinish}
// valveInfo={this.state.selectedValve, this.state.temperature, this.state.corosivity}
export default ActuatorModal;